import React, { useState, useEffect } from "react";
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import Page from "@/components/Page";
import Header from "@/components/Header";
// Removed Loading import - using skeleton loading instead
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Checkbox } from "@/components/ui/checkbox";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Shield, Users, Settings, Plus, Edit, Trash2, Save, RefreshCw, Key, CheckCircle } from "lucide-react";
import ProtectedRoute from "@/components/ProtectedRoute";
import PermissionRoute from "@/components/permissions/PermissionRoute";
import axiosInstance from "@/api/axios/axiosInstance";
import { toast } from "sonner";

interface Role {
  id: number;
  name: string;
  display_name: string;
  description: string;
  is_system_role: boolean;
  is_active: boolean;
  created_at: string;
  updated_at: string;
}

interface Permission {
  id: number;
  name: string;
  display_name: string;
  description: string;
  category: string;
  is_system_permission: boolean;
}



interface RolePermission {
  permission_id: number;
  permission_name: string;
}



// API Functions
const fetchRoles = async (): Promise<Role[]> => {
  const response = await axiosInstance.get("/api/roles");
  return response.data;
};

const fetchPermissions = async (): Promise<Permission[]> => {
  const response = await axiosInstance.get("/api/permissions");
  return response.data.data || response.data;
};



const fetchRolePermissions = async (roleId: number): Promise<RolePermission[]> => {
  const response = await axiosInstance.get(`/api/role-permissions/role/${roleId}`);
  return response.data.data || response.data;
};



const Roles: React.FC = () => {
  const [selectedRole, setSelectedRole] = useState<Role | null>(null);
  const [selectedPermissions, setSelectedPermissions] = useState<number[]>([]);

  const [isUpdating, setIsUpdating] = useState(false);
  const [searchQuery, setSearchQuery] = useState("");
  const [isCreateDialogOpen, setIsCreateDialogOpen] = useState(false);
  const [newRole, setNewRole] = useState({
    name: "",
    display_name: "",
    description: ""
  });

  const queryClient = useQueryClient();

  // Queries
  const { data: roles, isLoading: rolesLoading } = useQuery({
    queryKey: ["roles"],
    queryFn: fetchRoles,
  });

  const { data: permissions, isLoading: permissionsLoading } = useQuery({
    queryKey: ["permissions"],
    queryFn: fetchPermissions,
  });



  const { data: rolePermissions, isLoading: rolePermissionsLoading } = useQuery({
    queryKey: ["rolePermissions", selectedRole?.id],
    queryFn: () => fetchRolePermissions(selectedRole!.id),
    enabled: !!selectedRole,
  });



  // Update selected permissions when role changes
  useEffect(() => {
    if (rolePermissions) {
      setSelectedPermissions(rolePermissions.map(rp => rp.permission_id));
    }
  }, [rolePermissions]);



  // Mutations
  const updateRolePermissionsMutation = useMutation({
    mutationFn: async ({ roleId, permissionIds }: { roleId: number; permissionIds: number[] }) => {
      await axiosInstance.post(`/api/role-permissions/role/${roleId}/bulk`, {
        permission_ids: permissionIds
      });
    },
    onSuccess: () => {
      toast.success("Role permissions updated successfully");
      queryClient.invalidateQueries({ queryKey: ["rolePermissions"] });
      queryClient.invalidateQueries({ queryKey: ["pagePermissions"] });
    },
    onError: (error: any) => {
      toast.error(error.response?.data?.message || "Failed to update permissions");
    }
  });



  const createRoleMutation = useMutation({
    mutationFn: async (roleData: { name: string; display_name: string; description: string }) => {
      const response = await axiosInstance.post("/api/roles", roleData);
      return response.data;
    },
    onSuccess: () => {
      toast.success("Role created successfully");
      queryClient.invalidateQueries({ queryKey: ["roles"] });
      queryClient.invalidateQueries({ queryKey: ["pagePermissions"] });
      setIsCreateDialogOpen(false);
      setNewRole({ name: "", display_name: "", description: "" });
    },
    onError: (error: any) => {
      toast.error(error.response?.data?.message || "Failed to create role");
    }
  });

  // Handler functions
  const handleRoleSelect = (role: Role) => {
    setSelectedRole(role);
  };

  const handlePermissionToggle = (permissionId: number) => {
    setSelectedPermissions(prev =>
      prev.includes(permissionId)
        ? prev.filter(id => id !== permissionId)
        : [...prev, permissionId]
    );
  };



  const handleUpdatePermissions = async () => {
    if (!selectedRole) return;

    setIsUpdating(true);
    try {
      await updateRolePermissionsMutation.mutateAsync({
        roleId: selectedRole.id,
        permissionIds: selectedPermissions
      });
    } finally {
      setIsUpdating(false);
    }
  };

  const handleCreateRole = () => {
    createRoleMutation.mutate(newRole);
  };

  const filteredRoles = roles?.filter(role =>
    role.display_name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    role.name.toLowerCase().includes(searchQuery.toLowerCase())
  ) || [];

  // Removed loading screen for better UX

  return (
    <PermissionRoute requireSuperAdmin>
      <Page >
        <div aria-hidden className="absolute inset-0 pointer-events-none bg-gradient-to-r from-indigo-500/10 via-purple-500/10 to-pink-500/10" />
        <div className="relative z-10">
        <Header
          title="Roles & Permissions Management"
          buttonText="Create Role"
          action={() => setIsCreateDialogOpen(true)}
          filterByName={true}
          filterWord={searchQuery}
          onFilterChange={setSearchQuery}
          useSheet={false}
          showViewToggle={false}
          showFilters={false}
        />

        {/* Split Layout Container */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 h-[calc(100vh-200px)]">
          {/* Left Side - Roles List */}
          <div className="lg:col-span-1">
            <Card className="h-full bg-white/5 backdrop-blur-md border border-white/10">
              <CardHeader>
                <CardTitle className="flex items-center gap-2 text-white">
                  <Shield className="h-5 w-5 text-indigo-400" />
                  Roles ({filteredRoles.length})
                </CardTitle>
                <CardDescription className="text-gray-400">
                  Select a role to manage its permissions
                </CardDescription>
              </CardHeader>
              <CardContent className="p-0">
                <div className="max-h-[calc(100vh-350px)] overflow-y-auto">
                  {filteredRoles.map((role) => (
                    <div
                      key={role.id}
                      onClick={() => handleRoleSelect(role)}
                      className={`p-4 border-b border-white/10 cursor-pointer hover:bg-white/10 transition-colors ${
                        selectedRole?.id === role.id ? 'bg-indigo-500/20 border-l-4 border-l-indigo-500' : ''
                      }`}
                    >
                      <div className="flex items-center justify-between">
                        <div>
                          <h3 className="font-medium text-white">{role.display_name}</h3>
                          <p className="text-sm text-gray-400">{role.description}</p>
                          <div className="flex items-center gap-2 mt-1">
                            <Badge variant="outline" className="text-xs bg-white/5 border-white/20 text-gray-300">
                              #{role.id}
                            </Badge>
                            {role.is_system_role && (
                              <Badge variant="secondary" className="text-xs bg-purple-500/20 text-purple-300 border border-purple-500/30">
                                System
                              </Badge>
                            )}
                          </div>
                        </div>
                        {selectedRole?.id === role.id && (
                          <CheckCircle className="h-5 w-5 text-indigo-400" />
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Right Side - Permissions Panel */}
          <div className="lg:col-span-2">
            {selectedRole ? (
              <Card className="h-full bg-white/5 backdrop-blur-md border border-white/10">
                <CardHeader>
                  <div className="flex items-center justify-between">
                    <div>
                      <CardTitle className="flex items-center gap-2 text-white">
                        <Key className="h-5 w-5 text-indigo-400" />
                        Permissions for {selectedRole.display_name}
                      </CardTitle>
                      <CardDescription className="text-gray-400">
                        Manage permissions for this role
                      </CardDescription>
                    </div>
                    <Button
                      onClick={handleUpdatePermissions}
                      disabled={isUpdating}
                      className="flex items-center gap-2 bg-gradient-to-r from-indigo-600 to-purple-600 hover:from-indigo-500 hover:to-purple-500 text-white border-0"
                    >
                      {isUpdating ? (
                        <RefreshCw className="h-4 w-4 animate-spin" />
                      ) : (
                        <Save className="h-4 w-4" />
                      )}
                      {isUpdating ? 'Updating...' : 'Update Permissions'}
                    </Button>
                  </div>
                </CardHeader>
                <CardContent className="space-y-6">
                  {/* System Permissions Section */}
                  <div>
                    <h3 className="text-lg font-medium mb-3 flex items-center gap-2 text-white">
                      <Key className="h-4 w-4 text-indigo-400" />
                      System Permissions
                    </h3>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4 max-h-60 overflow-y-auto">
                      {permissions?.map((permission) => (
                        <div key={permission.id} className="flex items-center space-x-2 p-2 border border-white/10 rounded bg-white/5">
                          <Checkbox
                            id={`permission-${permission.id}`}
                            checked={selectedPermissions.includes(permission.id)}
                            onCheckedChange={() => handlePermissionToggle(permission.id)}
                            className="border-white/30 data-[state=checked]:bg-indigo-600 data-[state=checked]:border-indigo-600"
                          />
                          <div className="flex-1">
                            <Label
                              htmlFor={`permission-${permission.id}`}
                              className="text-sm font-medium cursor-pointer text-white"
                            >
                              {permission.display_name}
                            </Label>
                            <p className="text-xs text-gray-400">{permission.description}</p>
                            <Badge variant="outline" className="text-xs mt-1 bg-white/5 border-white/20 text-gray-300">
                              {permission.category}
                            </Badge>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>


                </CardContent>
              </Card>
            ) : (
              <Card className="h-full flex items-center justify-center bg-white/5 backdrop-blur-md border border-white/10">
                <CardContent className="text-center">
                  <Shield className="h-12 w-12 text-gray-500 mx-auto mb-4" />
                  <h3 className="text-lg font-medium text-white mb-2">
                    Select a Role
                  </h3>
                  <p className="text-gray-400">
                    Choose a role from the left panel to manage its permissions.
                  </p>
                </CardContent>
              </Card>
            )}
          </div>
        </div>

        {/* Create Role Dialog */}
        <Dialog open={isCreateDialogOpen} onOpenChange={setIsCreateDialogOpen}>
          <DialogContent className="bg-[#0e1340] border border-white/10 text-white">
            <DialogHeader>
              <DialogTitle className="text-white">Create New Role</DialogTitle>
              <DialogDescription className="text-gray-400">
                Create a new role with custom permissions and access levels.
              </DialogDescription>
            </DialogHeader>
            <div className="space-y-4">
              <div>
                <Label htmlFor="role-name" className="text-gray-300">Role Name</Label>
                <Input
                  id="role-name"
                  placeholder="e.g., manager"
                  value={newRole.name}
                  onChange={(e) => setNewRole(prev => ({ ...prev, name: e.target.value }))}
                  className="bg-white/5 border-white/20 text-white placeholder:text-gray-500 focus:border-indigo-500"
                />
              </div>
              <div>
                <Label htmlFor="role-display-name" className="text-gray-300">Display Name</Label>
                <Input
                  id="role-display-name"
                  placeholder="e.g., Manager"
                  value={newRole.display_name}
                  onChange={(e) => setNewRole(prev => ({ ...prev, display_name: e.target.value }))}
                  className="bg-white/5 border-white/20 text-white placeholder:text-gray-500 focus:border-indigo-500"
                />
              </div>
              <div>
                <Label htmlFor="role-description" className="text-gray-300">Description</Label>
                <Textarea
                  id="role-description"
                  placeholder="Describe the role's responsibilities..."
                  value={newRole.description}
                  onChange={(e) => setNewRole(prev => ({ ...prev, description: e.target.value }))}
                  className="bg-white/5 border-white/20 text-white placeholder:text-gray-500 focus:border-indigo-500"
                />
              </div>
              <div className="flex justify-end gap-2">
                <Button variant="outline" onClick={() => setIsCreateDialogOpen(false)} className="bg-transparent border-white/20 text-white hover:bg-white/10">
                  Cancel
                </Button>
                <Button
                  onClick={handleCreateRole}
                  disabled={!newRole.name || !newRole.display_name || createRoleMutation.isPending}
                  className="bg-gradient-to-r from-indigo-600 to-purple-600 hover:from-indigo-500 hover:to-purple-500 text-white border-0"
                >
                  {createRoleMutation.isPending ? 'Creating...' : 'Create Role'}
                </Button>
              </div>
            </div>
          </DialogContent>
        </Dialog>
        </div>
      </Page>
    </PermissionRoute>
  );
};

export default Roles;
